<template>
  <div id="tabla-personas">
    <div v-if="!personas.length" class="alert alert-info" role="alert">
      No se han agregado personas
    </div>
    <table class="table">
      <thead>
      <tr>
        <th>Nombre</th>
        <th>Apellido</th>
        <th>Email</th>
        <th>Acciones</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="persona in personas" :key="persona.id">
        <td v-if="editando === persona.id">
          <div class="input-group mb-3">
            <span class="input-group-text"><i class="bi bi-person-fill"></i></span>
            <input type="text" class="form-control" v-model="persona.nombre"/>
          </div>
        </td>
        <td v-else>
          {{ persona.nombre }}
        </td>
        <td v-if="editando === persona.id">
          <div class="input-group mb-3">
            <span class="input-group-text"><i class="bi bi-person-fill"></i></span>
            <input type="text" class="form-control" v-model="persona.apellido"/>
          </div>
        </td>
        <td v-else>
          {{ persona.apellido }}
        </td>
        <td v-if="editando === persona.id">
          <div class="input-group mb-3">
            <span class="input-group-text"><i class="bi bi-envelope-fill"></i></span>
            <input type="email" class="form-control" v-model="persona.email"/>
          </div>
        </td>
        <td v-else>
          {{ persona.email }}
        </td>
        <td v-if="editando === persona.id" class="text-center">
          <button class="btn btn-success" @click="guardarPersona(persona)"><i class="bi bi-save2-fill"></i> Guardar
          </button>
          &nbsp;
          <button class="btn btn-secondary ml-2" @click="cancelarEdicion(persona)"><i class="bi bi-x-circle-fill"></i>
            Cancelar
          </button>
        </td>
        <td v-else class="text-center">
          <button class="btn btn-danger" @click="$emit('eliminar-persona', persona.id)"><i class="bi bi-trash-fill"></i>
            Eliminar
          </button>&nbsp;
          <button class="btn btn-info ml-2" @click="editarPersona(persona)"><i class="bi bi-pencil-fill"></i> Editar
          </button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: "tabla-personas",
  props: {
    personas: Array,
  },
  data: () => {
    return {
      editando: null,
    }
  },
  mounted() {

  },
  methods: {
    editarPersona(persona) {
      this.personaEditada = Object.assign({}, persona);
      this.editando = persona.id;
    },
    guardarPersona(persona) {
      if (!persona.nombre.length || !persona.apellido.length || !persona.email.length) {
        return;
      }
      this.$emit('actualizar-persona', persona.id, persona);
      this.editando = null;
    },
    cancelarEdicion(persona) {
      Object.assign(persona, this.personaEditada);
      this.editando = null;
    },
  },
  computed: {}
}
</script>
