<template>
  <div id="formulario-persona">
    <form @submit.prevent="enviarFormulario">
      <div class="container">
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <label for="persona-nombre">Nombre</label>
              <div class="input-group mb-3">
                <span class="input-group-text"><i class="bi bi-person-fill"></i></span>
                <input
                    ref="nombre"
                    v-model="persona.nombre"
                    type="text"
                    class="form-control"
                    name="Persona[nombre]"
                    :class="{ 'is-invalid': procesando && nombreInvalido }"
                    @focus="resetEstado"
                    @keypress="resetEstado"
                    id="persona-nombre"/>
              </div>
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label for="persona-apellido">Apellido</label>
              <div class="input-group mb-3">
                <span class="input-group-text"><i class="bi bi-person-fill"></i></span>
                <input
                    ref="apellido"
                    v-model="persona.apellido"
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': procesando && apellidoInvalido }"
                    @focus="resetEstado"
                    @keypress="resetEstado"
                    name="Persona[apellido]"
                    id="persona-apellido"/>
              </div>
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label for="persona-email">Email</label>
              <div class="input-group mb-3">
                <span class="input-group-text"><i class="bi bi-envelope-fill"></i></span>
                <input
                    ref="email"
                    v-model="persona.email"
                    type="email"
                    class="form-control"
                    :class="{ 'is-invalid': procesando && emailInvalido }"
                    @focus="resetEstado"
                    @keypress="resetEstado"
                    name="Persona[email]"
                    id="persona-email"/>
              </div>
            </div>
          </div>
        </div>
        <hr>
        <div class="row">
          <div class="col-md-8"></div>
          <div class="col-md">
            <div class="form-group">
              <button class="btn btn-success"><i class="bi bi-plus-circle-fill"></i> Añadir persona</button>
            </div>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="row">
          <div class="col-md">
            <div v-if="error && procesando" class="alert alert-danger" role="alert">
              ¡Debes rellenar todos los campos!
            </div>
            <div v-if="correcto" class="alert alert-success" role="alert">
              ¡La persona ha sido agregada de manera exitosa!
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: "formulario-persona",
  props: [],
  mounted() {

  },
  data: () => {
    return {
      procesando: false,
      correcto: false,
      error: false,
      persona: {
        nombre: '',
        apellido: '',
        email: '',
      },
    }
  },
  methods: {
    enviarFormulario() {
      this.procesando = true;
      this.resetEstado();

      // Comprobamos la presencia de errores
      if (this.nombreInvalido || this.apellidoInvalido || this.emailInvalido) {
        this.error = true;
        return;
      }
      this.$emit('agregar-persona', this.persona);
      this.$refs.nombre.focus();

      this.error = false;
      this.correcto = true;
      this.procesando = false;

      // Restablecemos el valor de la variables
      this.persona = {
        nombre: '',
        apellido: '',
        email: '',
      }
    },
    resetEstado() {
      this.correcto = false;
      this.error = false;
    }
  },
  computed: {
    nombreInvalido() {
      return this.persona.nombre.length < 1;
    },
    apellidoInvalido() {
      return this.persona.apellido.length < 1;
    },
    emailInvalido() {
      return this.persona.email.length < 1;
    },
  }
}
</script>
<style scoped>
form {
  margin-bottom: 2rem;
}
</style>
